import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

interface LaTeXProps {
  children: string;
  block?: boolean;
  className?: string;
}

export default function LaTeX({ children, block = false, className = '' }: LaTeXProps) {
  // Remove $ delimiters if present
  const cleanedContent = children.replace(/^\$+|\$+$/g, '');
  
  if (block) {
    return (
      <div className={`my-4 ${className}`}>
        <BlockMath math={cleanedContent} />
      </div>
    );
  }
  
  return (
    <span className={className}>
      <InlineMath math={cleanedContent} />
    </span>
  );
}

// Helper component to render text with inline LaTeX
interface LaTeXTextProps {
  children: string;
  className?: string;
}

export function LaTeXText({ children, className = '' }: LaTeXTextProps) {
  // Split text by $ delimiters for inline math
  const parts = children.split(/(\$[^$]+\$)/g);
  
  return (
    <span className={className}>
      {parts.map((part, index) => {
        if (part.startsWith('$') && part.endsWith('$')) {
          return <LaTeX key={index}>{part}</LaTeX>;
        }
        return <span key={index}>{part}</span>;
      })}
    </span>
  );
}

